


import React, { useEffect, useState } from 'react'
import MyContext from './Mycontext'
import { fireDB } from '../firebase/FirebaseConfig';
import { Timestamp, addDoc, collection, onSnapshot, orderBy, query,setDoc,doc,deleteDoc } from 'firebase/firestore';
import { toast } from 'react-toastify';


export default function MyState(props) {
    const state = {
        name: "Kamal Nayan",
        class: "9 C"
    }
    const color = 'red'

    const [mode, setMode] = useState('light');
    const [loading, setLoading] = useState(false)

    const toggleMode = () => {
        if (mode === 'light') {
            setMode('dark');
            document.body.style.backgroundColor = 'rgb(17, 24, 39)';
        }
        else {
            setMode('light');
            document.body.style.backgroundColor = 'white';

        }
    }


    const [products, setProducts] = useState({
      title: null,
      price: null,
      imageUrl: null,
      category: null,
      description: null,
      time: Timestamp.now(),
      date: new Date().toLocaleString(
        "en-US",
        {
          month: "short",
          day: "2-digit",
          year: "numeric",
        }
      )})

  //  Add Product 
    const addProduct = async (e) => {
      e.preventDefault()
    if (products.title == null || products.price == null || products.imageUrl == null || products.category == null 
      || products.description == null) {
      return toast.error('Please fill all fields')
    }
    try {
      const productRef = collection(fireDB, "products")
        // console.log('productref is:',productRef);
      await addDoc(productRef, products)
      toast.success("Product Add successfully")

      setTimeout(() => {
        window.location.href = '/dashboard'
      }, 800);
      getProductData()
    } 
    catch (error) {
      console.log(error)
    }
    setProducts("")
  }

  const [product, setProduct] = useState([]);  


  // get product
   const getProductData = async () => {
    try {
      const q = query(collection(fireDB, "products"),
        orderBy("time"),
        // limit(5)
      );

      const data = onSnapshot(q, (QuerySnapshot) => {
        let productsArray = [];
        QuerySnapshot.forEach((doc) => {
          productsArray.push({ ...doc.data(), id: doc.id });
        });
        setProduct(productsArray)
      });
      return () => data;

    } catch (error) {
      console.log(error)
    }
  }

  useEffect(() => {
    getProductData();
  }, []);


  // update product
  const edithandle = (item) => {
    setProducts(item)
  }

  const updateProduct = async (e) => {
    e.preventDefault()

    try {
      await setDoc(doc(fireDB, "products", products.id), products);
      toast.success("Product Updated successfully")
      getProductData();
      setTimeout(() => {
        window.location.href = '/dashboard'
      }, 800);
    } catch (error) {
      console.log(error)
    }
    setProducts("")
  }

  //delete product
  const deleteProduct = async (item) => {

    try {
      setLoading(true)
      await deleteDoc(doc(fireDB, "products", item.id));
      toast.success('Product Deleted successfully')
      setLoading(false)
      getProductData()
    } catch (error) {
      // toast.success('Product Deleted Falied')
      setLoading(false)
    }
  }

  const [searchkey, setSearchkey] = useState('')
  const [filterType, setFilterType] = useState('')
  const [filterPrice, setFilterPrice] = useState('')

  return (
    <>
     <MyContext.Provider value={{ mode, toggleMode,products,product, setProducts,addProduct,edithandle,updateProduct,deleteProduct,searchkey, setSearchkey,filterType, setFilterType,
      filterPrice, setFilterPrice }}>
      {props.children}
    </MyContext.Provider>
    </>
  )
}
