


import React, { useContext, useEffect } from 'react'
import myContext from '../../Context/Mycontext'
import { useDispatch, useSelector } from 'react-redux'
import { toast } from 'react-toastify'
import { addToCart } from '../../Redux/cartSlice'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import style from './style.css'

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

export default function MyCard() {

    const context = useContext(myContext)
    const { mode, product, searchkey,filterType,filterPrice } = context;

    const dispatch = useDispatch()
    const cartItems = useSelector((state) => state.cart)
    // console.log(cartItems)

    // add to cart
    const addCart = (product) => {
        dispatch(addToCart(product))
        toast.success('add to cart');
    }

    useEffect(() => {
        // localStorage.setItem('cart', JSON.stringify(cartItems));
    }, [cartItems])
//   console.log(product);


const options = {
    margin: 30,
    responsiveClass: true,
    nav: true,
    dots:false,
    autoplay: true,
    smartSpeed: 1000,
    responsive: {
      0: {
        items: 1,
      },
      400: {
        items: 1,
      },
      600: {
        items: 1,
      },
      700: {
        items: 1,
      },
      800: {
        items: 2,
      },
      1000: {
        items: 4,
      }
    }
}
  

  return (
    <>
    
    <section className="text-gray-600 body-font mt-5 card-sec">
            <div className="container  py-8 md:py-16 mx-auto" style={{maxWidth:'1700px'}}>
                <div class="lg:w-1/2 w-full mb-6 lg:mb-10">
                    <h1 class="sm:text-3xl text-2xl font-medium title-font m-0 text-start text-gray-900" style={{ color: mode === 'dark' ? 'white' : '' }}>Our Latest Collection</h1>
                    <div class="h-1 w-20 bg-pink-600 rounded mt-2 mb-5"></div>
                </div>

                <div className='row justify-content-evenly'>
                    
                    {
                         product.filter((obj) => obj.title.toLowerCase().includes(searchkey))
                        .filter((obj) => obj.category.toLowerCase().includes(filterType))
                        .filter((obj) => obj.price.includes(filterPrice)).map((item, index) => {
                            const { title, price, description, imageUrl,id } = item;
                            return(
                                
                                <div className='card col-3 p-0 border-0 shadow' 
                                key={index} style={{width:'23%'}} onClick={()=> window.location.href = `/info/${item.id}`}>
                                    <div key={index} className="border-2 hover:shadow-gray-100 hover:shadow-2xl transition-shadow duration-300 ease-in-out    border-gray-200 border-opacity-60 rounded-2xl overflow-hidden" style={{ backgroundColor: mode === 'dark' ? 'rgb(46 49 55)' : '', color: mode === 'dark' ? 'white' : '', }} >


                                    <div className="flex justify-center cursor-pointer p-5" style={{background:'#f2f2f25e'}}>
                                        <img className="w-100  ease-in-out" src={imageUrl} alt="blog" />
                                    </div>

                                    <div className="p-1 border-t-2">
                                        <h4 className="text-start fs-5 py-2" style={{ color: mode === 'dark' ? 'white' : '', }}>{title}</h4>
                                        
                                        <p className="text-start mb-3 px-2" style={{fontSize:'14px'}}>{description}</p>
                                        <p className="leading-relaxed mb-3 text-start px-2" style={{ color: mode === 'dark' ? 'white' : '' }}>
                                            Price: {price}</p>

                                       
                                    </div>

                                    </div>
                                </div>
                            )
                        })
                    }
                


                </div>

        

                

            </div>
        </section >
    
    </>
  )
}
