

import React from 'react'
import { BrowserRouter as Router,Routes,Route } from 'react-router-dom'
import Home from '../Pages/Home'
import Order from '../Pages/Order'
import Cart from '../Pages/Cart'
import Dashboard from '../Auth/Dashboard'
import Error from '../Pages/Error'
import MyState from '../../Context/MyState'
import Signup from '../Auth/Signup'
import Login from '../Auth/Login'
import ProductInfo from '../Pages/ProductInfo'
import AddProduct from '../Pages/AddProduct'
import UpdateProduct from '../Pages/UpdateProduct'
import { Navigate } from 'react-router-dom'
import AllProducts from '../Pages/AllProducts'
import SubProduct from '../Pages/SubProduct'

import NewCart from '../Pages/NewCart'

export default function Routing() {
  return (
    <>

      <MyState>
      <Router>
        <Routes>
          <Route path="" element={<Home/>} />
          <Route path='newcart' element={<NewCart/>} />
          <Route path='all' element={<AllProducts />} />
          <Route path='all/sub/:id' element={<SubProduct />} />
          <Route path="order" element={
            <ProtectedRoutes>
              <Order/>
            </ProtectedRoutes>
          } />
          <Route path="cart" element={<Cart/>} />
          <Route path="dashboard" element={
            <ProtectedRoutesForAdmin>
               <Dashboard/>
            </ProtectedRoutesForAdmin>
          } />
          <Route path='info/:id' element={<ProductInfo />} />
          <Route path='add' element={
            <ProtectedRoutesForAdmin>
              <AddProduct />
            </ProtectedRoutesForAdmin>
          } />
          <Route path='update' element={
            <UpdateProduct />
          } />
          <Route path='signup' element={<Signup />} />
          <Route path='login' element={<Login />} />
          <Route path="*" element={<Error/>} />
        </Routes>
      </Router>
      </MyState>
    </>
  )
}

//user

export const ProtectedRoutes = ({ children }) => {
  if (localStorage.getItem('user')) {
    return children
  }
  else {
    return <Navigate to='/login' />
  }
}


//admin

export const ProtectedRoutesForAdmin = ({children}) => {
  const admin = JSON.parse(localStorage.getItem('user'))
  // console.log(admin.user.email)
  if (admin.user.email === 'rb517454@gmail.com') {
    return children
  }
  else {
    return <Navigate to='/login' />
  }
}
