

import { useContext, useState } from 'react';
import { Link } from 'react-router-dom'
import myContext from '../../Context/Mycontext';
import { toast } from 'react-toastify';
import { auth, fireDB } from '../../firebase/FirebaseConfig'
import { createUserWithEmailAndPassword } from 'firebase/auth';
import { Timestamp, addDoc, collection } from 'firebase/firestore';

import style from './style.css'

export default function Signup() {

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const context = useContext(myContext);
    const { loading, setLoading } = context;

    const signup= async (e)=>{
      e.preventDefault();
      // console.log(name,email,password);
 
        if (name === "" || email === "" || password === "") {
            return toast.error("All fields are required")
        }

        try {
          const users = await createUserWithEmailAndPassword(auth, email, password);

          console.log(users)

          const user = {
              name: name,
              uid: users.user.uid,
              email: users.user.email,
              time : Timestamp.now()
          }
          localStorage.setItem('user-details',JSON.stringify(user.name));
          // console.log(user.name);
          
          const userRef = collection(fireDB, "users")
          await addDoc(userRef, user);
          toast.success("Signup Succesfully")
          setName("");
          setEmail("");
          setPassword("");
          
      } catch (error) {
          console.log(error)
      }  
      
    }

  return (
    <>
    
    <form className="login"  >
    <h2>Welcome, User!</h2>
    <p>Please Register in</p>
    <input type="text" name='name' placeholder="User Name"   value={name} onChange={(e) => setName(e.target.value)} />
    <input type="text" name='email' placeholder="User Email"  value={email}  onChange={(e) => setEmail(e.target.value)}/>
    <input type="password" name='password' placeholder="Password"  value={password} onChange={(e) => setPassword(e.target.value)}/>
    <input type="submit" value="Register" onClick={signup} />
    <div className="links">
        <a href="">Already Rigister!</a>
        <Link to='/login'>Login</Link>
    </div>
    </form>
    
    </>
  )
}
