

import React, { useContext, useEffect, useState } from 'react'
import style from './style.css'
import Common from '../Layouts/Common'
import { useDispatch, useSelector } from 'react-redux'
import { addToCart, deleteFromCart } from '../../Redux/cartSlice'
import myContext from '../../Context/Mycontext'
import { Navigate, useParams } from 'react-router-dom'
import { toast } from 'react-toastify'
import { doc,getDoc } from  'firebase/firestore';
import { fireDB } from '../../firebase/FirebaseConfig';

export default function ProductInfo() {

    const context = useContext(myContext)
    const { mode } = context;

    const params = useParams()
    const [products,setProducts] = useState()

    const dispatch = useDispatch()
    const cartItems = useSelector((state) => state.cart)
    // console.log('ffffffffs',cartItems)


    const getProductData = async () => {
        try {
            const productTemp = await getDoc(doc(fireDB, "products", params.id))
            // console.log(productTemp)
            setProducts(productTemp.data());
            // console.log(productTemp.data())
        } catch (error) {
            console.log(error)
        }
    }

    useEffect(() => {
        getProductData()
    }, [])


    // add to cart
    const addmyCart = (product) => {
        dispatch(addToCart(product))
        toast.success('add to cart');
    }


    useEffect(() => {
        localStorage.setItem('cart', JSON.stringify(cartItems));
        // console.log('ggggggggg',cartItems);
        
    }, [cartItems])
   
  return (
    <>
    
    <Common>

    <section className="text-gray-600 body-font overflow-hidden mt-5 mb-5">
        <div className="container px-5 py-32 mx-auto">


            <div className='row'>

                <div className='col-6'>

                <div className="lg:w-4/5 mx-auto flex flex-wrap">
                <img
                    alt="ecommerce"
                    className="img-fluid"
                    src={products?.imageUrl}
                />

                </div>
            </div>


                <div className='col-6 info-left'>

                <div className="lg:w-1/2 w-full lg:pl-10 lg:py-6 mt-6 lg:mt-0">
                    <p className="text-sm title-font text-gray-500 tracking-widest" >
                       {products?.category}                
                    </p>
                    <h1 className="text-gray-900 text-start mb-1" style={{ color: mode === 'dark' ? 'white' : '', }}>
                        {products?.title}
                    </h1>
                    <div className="d-flex  mb-4" style={{ color: mode === 'dark' ? 'white' : '', }}>
                        <span className="d-flex items-center">
                            <svg
                                fill="currentColor"
                                stroke="currentColor"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                className="w-4 h-4 text-indigo-500"
                                viewBox="0 0 24 24"
                            >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                            </svg>
                            <svg
                                fill="currentColor"
                                stroke="currentColor"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                className="w-4 h-4 text-indigo-500"
                                viewBox="0 0 24 24"
                            >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                            </svg>
                            <svg
                                fill="currentColor"
                                stroke="currentColor"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                className="w-4 h-4 text-indigo-500"
                                viewBox="0 0 24 24"
                            >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                            </svg>
                            <svg
                                fill="currentColor"
                                stroke="currentColor"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                className="w-4 h-4 text-indigo-500"
                                viewBox="0 0 24 24"
                            >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                            </svg>
                            <svg
                                fill="none"
                                stroke="currentColor"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                className="w-4 h-4 text-indigo-500"
                                viewBox="0 0 24 24"
                            >
                                <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z" />
                            </svg>
                            <span className="text-gray-600 mt-2"> &nbsp; &nbsp; 4 Reviews</span>
                        </span>
                        
                    </div>
                    <p className="leading-relaxed border-b-2 pb-5 "style={{ color: mode === 'dark' ? 'white' : '', }}>
                        {products?.description}
                    </p>
                    
                    <div className="d-flex justify-content-evenly" style={{ color: mode === 'dark' ? 'white' : '', }}>
                        <span className="title-font font-medium text-2xl text-gray-900">
                           Price: $ {products?.price}
                        </span>

                        {/* <p>Quantity: {products.index}</p> */}
                        <button className="bg-pink-600 h-100 text-white py-2" onClick={() => addmyCart(products)}>
                            Add To Cart
                        </button>
                        
                    </div>
                </div>

                </div>
  
            </div>
        </div>
    </section>

    </Common>
    
    </>
  )
}
