

import React, { useContext, useEffect, useState } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import myContext from '../../Context/Mycontext';
import { MdOutlineProductionQuantityLimits } from 'react-icons/md';
import { FaUser, FaCartPlus } from 'react-icons/fa';
import { AiFillShopping, AiFillPlusCircle, AiFillDelete } from 'react-icons/ai';
import Table from 'react-bootstrap/Table';
import { Link } from 'react-router-dom';

function DashboardTab() {
    const context = useContext(myContext)
    const { mode,product,edithandle,updateProduct,deleteProduct } = context
    console.log('products',product);
    
    // let [isOpen, setIsOpen] = useState(false)

    // function closeModal() {
    //     setIsOpen(false)
    // }

    // function openModal() {
    //     setIsOpen(true)
    // }

    const addProduct =()=>{
        window.location.href = '/add'
    }
    return (
        <>
            <div className="container mx-auto mt-5">
                <div className="tab mx-auto ">
                    <Tabs defaultIndex={0} className=" ">

                        {/* <TabList className="d-flex justify-content-center gap-5">

                            <Tab className='list-unstyled'>
                                <button type="button" className="px-5 py-3 border-0 shadow">
                                    <div className="flex gap-2 items-center">
                                        <MdOutlineProductionQuantityLimits />Products</div> </button>
                            </Tab>

                            <Tab className='list-unstyled'>
                                <button type="button" className="px-5 py-3 border-0 shadow">
                                    <div className="flex gap-2 items-center">
                                        <AiFillShopping /> Order
                                    </div>
                                </button>
                            </Tab>

                            <Tab className='list-unstyled'>
                                <button type="button" className="px-5 py-3 border-0 shadow">
                                    <div className="flex gap-2 items-center">
                                        <FaUser /> Users
                                    </div>
                                </button>
                            </Tab>
                        </TabList> */}


                {/* product  */}
                <TabPanel>
                    <div className='  px-4 md:px-0 mt-5'>
                        <h1 className='text-start mb-5 text-3xl font-semibold underline' 
                        style={{ color: mode === 'dark' ? 'white' : '' }}>Product Details</h1>

                        <div className="d-flex justify-content-end mb-5">
                            <button type="button" onClick={addProduct}
                                className="py-3 border-0 px-3 bg-pink-600 h-100 text-white" style={{ backgroundColor: mode === 'dark' ? 'rgb(46 49 55)' : '', color: mode === 'dark' ? 'white' : '', }} > <div className="flex gap-2 items-center">
                                    Add Product <FaCartPlus size={20} />
                                </div></button>
                        </div>

                        <div className="relative overflow-x-auto ">

                        <Table striped bordered hover>
                        <thead>
                            <tr>
                            <th>S.No</th>
                            <th>Image</th>
                            <th>Title</th>
                            <th>Price</th>
                            <th>Category</th>
                            <th>Description</th>
                            <th>Date</th>
                            <th>Action</th>
                            </tr>
                        </thead>
                        {
                            product.map((item,index)=>{
                                const {title,price,imageUrl,description,category,date} = item
                                return(
                            <tbody key={index}>
                            <tr>
                            <td>{index + 1}</td>
                            <td><img className='w-50' src={imageUrl} alt="img" /></td>
                            <td>{title}</td>
                            <td>{price}</td>
                            <td>{category}</td>
                            <td>{description}</td>
                            <td>{date}</td>
                            <td>
                            <div className=" d-flex gap-2 cursor-pointer text-black " style={{ color: mode === 'dark' ? 'white' : '' }}>
                                <div onClick={()=>deleteProduct(item)} >
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-25">
                                        <path strokeLinecap="round" strokeLinejoin="round" d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0" />
                                    </svg>
                                </div>

                                <Link to='/update'>
                                <div onClick={edithandle(item)} >
                                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-25">
                                        <path strokeLinecap="round" strokeLinejoin="round" d="M16.862 4.487l1.687-1.688a1.875 1.875 0 112.652 2.652L10.582 16.07a4.5 4.5 0 01-1.897 1.13L6 18l.8-2.685a4.5 4.5 0 011.13-1.897l8.932-8.931zm0 0L19.5 7.125M18 14v4.75A2.25 2.25 0 0115.75 21H5.25A2.25 2.25 0 013 18.75V8.25A2.25 2.25 0 015.25 6H10" />
                                    </svg>
                                </div>
                                </Link>

                            </div>
                            </td>
                            </tr>
                            
                        </tbody>
                                )
                            })
                        }
                        </Table>  

                        </div>
                    </div>
                </TabPanel>
                <TabPanel>
                    {/* <Order order={order} setOrder={setOrder} setLoading={setLoading} /> */}
                    <div className="relative overflow-x-auto mb-16">
                        <h1 className=' text-center mb-5 text-3xl font-semibold underline' style={{ color: mode === 'dark' ? 'white' : '' }}>Order Details</h1>
                                
                        <Table striped bordered hover>
                        <thead>
                            <tr>
                            <th>S.No</th>
                            <th>Name</th>
                            <th>Address</th>
                            <th>Pincode</th>
                            <th>Phone Number</th>
                            <th>Email</th>
                            <th>Date</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                            <td>1</td>
                            <td>name</td>
                            <td>address</td>
                            <td>123456</td>
                            <td>123456789</td>
                            <td>demo@gmail.com</td>
                            <td>5th Aug,2024</td>
                            </tr>
                            
                        </tbody>
                        </Table>        
                    
                    </div>
                </TabPanel>
                <TabPanel>
                    {/* <User addressInfo={addressInfo} setAddressInfo={setAddressInfo} setLoading={setLoading} /> */}
                    <div className="relative overflow-x-auto mb-10">
                        <h1 className=' text-center mb-5 text-3xl font-semibold underline' style={{ color: mode === 'dark' ? 'white' : '' }}>User Details</h1>

                        <Table striped bordered hover>
                        <thead>
                            <tr>
                            <th>#</th>
                            <th>First Name</th>
                            <th>Last Name</th>
                            <th>Username</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                            <td>1</td>
                            <td>Mark</td>
                            <td>Otto</td>
                            <td>@mdo</td>
                            </tr>
                            <tr>
                            <td>2</td>
                            <td>Jacob</td>
                            <td>Thornton</td>
                            <td>@fat</td>
                            </tr>
                            <tr>
                            <td>3</td>
                            <td colSpan={2}>Larry the Bird</td>
                            <td>@twitter</td>
                            </tr>
                        </tbody>
                        </Table>  

                    </div>
                </TabPanel>

            </Tabs>
        </div>
    </div>
        </>
    )
}


export default DashboardTab