

import React, { useContext } from 'react'
import myContext from '../../Context/Mycontext'

function UpdateProduct() {

    const context = useContext(myContext);
    const {products,setProducts,updateProduct} = context

    return (
        <div>
        <div className=' flex justify-center items-center h-screen'>
            <div className=' bg-gray-800 px-10 py-10 rounded-xl '>
                
            <form className="login">
                <h2>Welcome, User!</h2>
                <p>update Your Products</p>

                <input type="text"  name='title' placeholder="Product Title"  
                onChange={(e) => setProducts({ ...products, title: e.target.value })} value={products.title}/>

                <input type="text" name='price' placeholder="Product Price"   onChange={(e) => setProducts({ ...products, price: e.target.value })} value={products.price}/>

                <input type="text" name='imageurl' placeholder="Product ImageUrl" onChange={(e) => setProducts({ ...products, imageUrl: e.target.value })} value={products.imageUrl}/>

                <input type="text"  name='category' placeholder="Product Category"  onChange={(e) => setProducts({ ...products, category: e.target.value })} value={products.category} />

                <textarea cols="41" rows="5" name='desc'className=' bg-gray-600 mb-4' 
                placeholder=' Product desc...' onChange={(e) => setProducts({ ...products, description: e.target.value })} 
                value={products.description}></textarea>

                <input type="submit" value="Update Product"  onClick={updateProduct} />
            </form>
             
            </div>
        </div>
    </div>
    )
}

export default UpdateProduct